@use "../base/variables" as v;
@use "../base/mixins" as m;

html {
  box-sizing: border-box;
}

body {
  background-color: v.$dLight;
  font-family: v.$font;
  display: grid;
  height: 100vh;
  place-items: center;
  box-sizing: border-box;
  max-width: 100vw;
  height: 100vh;
}

main {
  width: 100%;
  @include m.tablet {
    width: auto;
  }
}

.calculator-container {
  margin: 0;
  margin-top: 2rem;
  padding: 2rem;
  background-color: v.$neutral;

  @include m.tablet {
    width: auto;
    margin: 0 auto;
    padding: 2.5rem;
    border-radius: 2rem;
  }
}
