@use "../base/variables" as v;

.screen {
  padding: 1.2rem;
  border-radius: 0.5rem;
  background-color: v.$light;
  height: 3.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  box-shadow: inset 0px 0px 18px 1px rgba(0, 0, 0, 0.75);

  .high-screen {
    text-align: end;
    display: block;
    height: 1.5rem;
  }

  .low-screen {
    font-weight: 600;
    font-size: 1.6rem;
    text-align: end;
    display: block;
    height: 2rem;
  }
}
