html {
  box-sizing: border-box;
}

body {
  background-color: #cbe9e9;
  font-family: "Rubik", sans-serif;
  display: grid;
  height: 100vh;
  place-items: center;
  box-sizing: border-box;
  max-width: 100vw;
  height: 100vh;
}

main {
  width: 100%;
}
@media (min-width: 786px) {
  main {
    width: auto;
  }
}

.calculator-container {
  margin: 0;
  margin-top: 2rem;
  padding: 2rem;
  background-color: #6797a3;
}
@media (min-width: 786px) {
  .calculator-container {
    width: auto;
    margin: 0 auto;
    padding: 2.5rem;
    border-radius: 2rem;
  }
}

.screen {
  padding: 1.2rem;
  border-radius: 0.5rem;
  background-color: #b9e2e2;
  height: 3.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  box-shadow: inset 0px 0px 18px 1px rgba(0, 0, 0, 0.75);
}
.screen .high-screen {
  text-align: end;
  display: block;
  height: 1.5rem;
}
.screen .low-screen {
  font-weight: 600;
  font-size: 1.6rem;
  text-align: end;
  display: block;
  height: 2rem;
}

.buttons-container {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 0.5rem;
  row-gap: 0.3rem;
}
@media (min-width: 786px) {
  .buttons-container {
    column-gap: 0.8rem;
    row-gap: 0.5rem;
  }
}
.buttons-container button {
  text-align: center;
  border-radius: 0.3rem;
  border: none;
  color: #282832;
  font-family: "Rubik", sans-serif;
  font-weight: 700;
  padding: 0 0.2rem;
  height: 4rem;
}
@media (min-width: 786px) {
  .buttons-container button {
    width: 5rem;
    height: 2.3rem;
  }
}
.buttons-container button:active {
  color: #fff;
  background-color: #282832;
}
.buttons-container button:hover {
  box-shadow: 0px 0px 18px -6px rgba(0, 0, 0, 0.75);
}
.buttons-container .btn-delete {
  background-color: #a92727;
}
.buttons-container .btn-clear {
  background-color: #282832;
}
.buttons-container .btn-equals {
  background-color: darkgreen;
}
.buttons-container .btn-answer {
  background-color: darkorange;
}
.buttons-container .btn-answer,
.buttons-container .btn-clear,
.buttons-container .btn-delete,
.buttons-container .btn-equals {
  color: #fff;
}
.buttons-container .btn-answer:active,
.buttons-container .btn-clear:active,
.buttons-container .btn-delete:active,
.buttons-container .btn-equals:active {
  color: #282832;
  background-color: #b9e2e2;
}