@use "../base/variables" as v;
@use "../base/mixins" as m;

.buttons-container {
  padding-top: 2rem;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 0.5rem;
  row-gap: 0.3rem;

  @include m.tablet {
    column-gap: 0.8rem;
    row-gap: 0.5rem;
  }

  button {
    text-align: center;
    border-radius: 0.3rem;
    border: none;
    color: v.$dark;
    font-family: v.$font;
    font-weight: 700;
    padding: 0 0.2rem;
    height: 4rem;

    @include m.tablet {
      width: 5rem;
      height: 2.3rem;
    }

    &:active {
      color: v.$white;
      background-color: v.$dark;
    }

    &:hover {
      box-shadow: 0px 0px 18px -6px rgba(0, 0, 0, 0.75);
    }
  }

  .btn-delete {
    background-color: v.$red;
  }

  .btn-clear {
    background-color: v.$dark;
  }

  .btn-equals {
    background-color: darkgreen;
  }

  .btn-answer {
    background-color: darkorange;
  }

  .btn-answer,
  .btn-clear,
  .btn-delete,
  .btn-equals {
    @include m.specialBtns;
  }
}
